  .project {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
    }
    
    .project h1 {
    font-size: 2rem;
    margin: 20px 0;
    }
    
    .project img {
    width: 100%;
    max-width: 600px;
    margin-bottom: 20px;
    }
    
    .project a {
    display: inline-block;
    padding: 10px 20px;
    margin-top: 10px;
    background-color: #333;
    color: #fff;
    text-decoration: none;
    font-size: 1.2rem;
    transition: background-color 0.3s ease;
    }
    
    .project a:hover {
    background-color: #fff;
    color: #333;
    }
    
    
    
    
    
    